/* RightDiv.css */

.login-container {
    
    display: flex;
    flex: 1;
    padding: 10%;
    box-sizing: border-box;
    background-color: transparent;
    font-family: orbitron;
    font-size: large;
    color: white;   
    justify-content: center;
    align-items: center;
  }
  
  .login-form-container {
    background: transparent;
    box-shadow: 0 4px 8px rgba(255, 255, 255, 0.7);
    padding: 5%;
  }
  
  .login-title {
    color: white;
    text-align: center;
    margin-bottom: 10%;
  }
  
  .login-form-button {
    width: 100%;
  }
  
  .login-form-forgot {
    float: right;
    color: white;
  }
  
  .login-form-button {
    background: white;
    border: 1px solid white;
    color: #001529;
  }
  
  .login-form-button:hover {
    background: #001529;
    color: white;
    border: 1px solid white;
  }